import React from 'react'
import './contact.css'

import { AiOutlineMail, AiOutlinePhone, AiOutlineMessage, AiOutlineLinkedin } from 'react-icons/ai'

const Contact = () => {
  return (
    <section id='contact'>
      <div className="introtext"><h1>Contact Me</h1></div>

      <div className="container contact__container">
        <div className="contact__options">
          <article className="contact__option">
            <AiOutlineMail/>
            <h4>Email</h4>
            <h5>nb726@scarletmail.rutgers.edu</h5>
            <a href="mailto:nb726@scarletmail.rutgers.edu">Send an email</a>
          </article>

          <article className="contact__option">
            <AiOutlineMessage/>/<AiOutlinePhone/>  
            <h4>Phone Number</h4>
            <h5>1-201-408-7000</h5>
            <a href="tel:0012014087000">Call&emsp;</a>
            <a href="sms:0012014087000">Text</a>
          </article>

          <article className="contact__option">
            <AiOutlineLinkedin/>
            <h4>LinkedIn</h4>
            <a href="https://www.linkedin.com/in/namanbajaj">Connect on LinkedIn</a>
          </article>
        </div>

        <form action=""></form>
      </div>
    </section>
  )

}

export default Contact